window.dom = {
    /**
     * ÂàõÂª∫ËäÇÁÇπ
     */
    create(string) {
        const container = document.createElement("template")
        container.innerHTML = string.trim()
        console.log(container)
        return container.content.firstChild
    },
    /**
     * Âú®‰∏Ä‰∏™ËäÇÁÇπÂêéÈù¢Êñ∞Â¢û‰∏Ä‰∏™ËäÇÁÇπ
     */
    after(node, newNode) {
        node.parentNode.insertBefore(newNode, node.nextSibling)
    },
    /**
     * Âú®‰∏Ä‰∏™ËäÇÁÇπÁöÑÂâçÈù¢ÊèíÂÖ•‰∏Ä‰∏™ËäÇÁÇπ
     */
    before(node, newNode) {
        node.parentNode.insertBefore(newNode, node)
    },
    /**
     * Âú®‰∏Ä‰∏™Áà∂ËäÇÁÇπÈáåÈù¢Êñ∞Â¢û‰∏Ä‰∏™Â≠êËäÇÁÇπ
     */
    append(parent, newNode) {
        parent.appendChild(newNode)
    },
    /**
     * Âú®‰∏Ä‰∏™ËäÇÁÇπÂ§ñÈù¢Êñ∞Â¢û‰∏Ä‰∏™Áà∂ËäÇÁÇπÔºå‰æãÂ¶Ç:divÂ§ñÈù¢ÂÜçÊ∑ªÂä†‰∏ÄÂ±Çdiv
     * ÂéüÁêÜ:
     * ÂÖàÊääparentÊîæÂà∞nodeÁöÑÂâçÈù¢
     * ÁÑ∂ÂêéÊäänodeÊîæÂà∞parentÁöÑÈáåÈù¢
     */
    wrap(node, parent) {
        dom.before(node, parent)
        dom.append(parent, node)
    },
    /**
     * Âà†Èô§ËäÇÁÇπ
     * ËøôÊòØÂÖºÂÆπIEÁöÑÂÜôÊ≥ï,ÈÄöËøáÁà∂ËäÇÁÇπÂà†Èô§Â≠êËäÇÁÇπ
     */
    remove(node) {
        node.parentNode.removeChild(node)
        return node
    },
    /**
     * Âà†Èô§Âêé‰ª£
     */
    empty(node) {
        const { childNodes } = node
        const array = []
            // for (let i = 0; i < childNodes.length - 1; i++) {
            //     dom.remove(childNodes[i])
            //     array.push(childNodes[i])
            // }
            // return array
        let x = node.firstChild
        while (x) {
            array.push(dom.remove(node.firstChild))
            x = node.firstChild
        }
        return array
    },
    /**
     * ‰øÆÊîπËäÇÁÇπ
     * Â¶ÇÊûúÂèÇÊï∞‰∏∫3‰∏™,ÂàÜÂà´ÊòØËäÇÁÇπÂêçÁß∞„ÄÅÂ±ûÊÄßÂêç„ÄÅÂ±ûÊÄßÂÄºÔºåÂàôÊòØ‰øÆÊîπËäÇÁÇπ
     * Â¶ÇÊûúÂèÇÊï∞‰∏∫2‰∏™,ÂàÜÂà´ÊòØËäÇÁÇπÂêçÁß∞„ÄÅÂ±ûÊÄßÂêçÔºåÂàôÊòØÊü•ËØ¢ËäÇÁÇπ
     */
    attr(node, name, value) { //ÈáçËΩΩ
        if (arguments.length === 3) {
            node.setAttribute(name, value)
        } else if (arguments.length === 2) {
            return node.getAttribute(name)
        }
    },
    /**
     * ËÆæÁΩÆËäÇÁÇπÁöÑÂÜÖÂÆπ
     */
    text(node, string) { //ÈÄÇÈÖç
        if ('innerText' in node) {
            node.innerText = string
        } else {
            node.textContent = string
        }
    },
}